 Set the toolbar background to transparent when not scrolling
*/
nav.landing-page-toolbar.theme--dark.toolbar-top {
  background-color:transparent!important;
}
/*
 Set the toolbar background to white when scrolling
*/
nav.landing-page-toolbar.theme--light.toolbar-scroll {
  background-color:white!important;
}
/*
 Change the toolbar to black when scrolling
*/
nav.landing-page-toolbar.toolbar-scroll .toolbar-logo {
  background-size: 35px 35px;
  background-image: url(data:image/svg+xml;base64,);
  background-position: 16px 11.5px;
}
/*
 Toolbar logo
*/
nav.landing-page-toolbar .toolbar-logo {
  background-size: 35px 35px;
  background-image: url(data:image/svg+xml;base64,);
  background-position: 16px 11.5px;
}
/*
 Toolbar logo text, hidden on super small display
*/
@media screen and (min-width: 376px) {
nav.landing-page-toolbar .toolbar-logo .btn__content:after {
    text-transform: none;
    padding-left:65px;
    font-weight: 500;
    content: "helidon.io";
}
}
/*
 Force toolbar button text to be capitalized instead of all caps
*/
nav.landing-page-toolbar .toolbar__items .btn {
  text-transform: none;
  font-size: 16px;
  font-weight: 400;
}
/*
 Remove transition on icons when scrolling
*/
nav.landing-page-toolbar .toolbar__items i {
   -webkit-transition: unset!important;
   transition: unset!important;
}
/*
  Force the content wrap to go over the toolbar space
  This allows to have a transparent background on the toolbar
*/
.content--wrap {
  margin-top: -58px;
}
/*
 1st screen
*/
.screen-1 {
  padding:0px !important;
}
.screen-1 .screen-wrapper {
  position:relative;
}
.screen-1 .bgd {
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
  background-image: linear-gradient(19deg, #4174d8, #4accf8);
}
.screen-1 .sections {
  position:relative;
  padding-bottom: 75px;
}
.screen-1 .jumbotron {
  margin-top: 150px;
  margin-bottom: 50px;
  text-align:center;
}
.screen-1 .jumbotron h1 {
  color: white;
  font-size:56px;
  font-weight:300;
}
.screen-1 .jumbotron p {
  color: rgba(255,255,255,.5);
  font-size: 22px;
  font-weight: 400;
}
.screen-1 .section {
  margin-left:10%;
  margin-right:10%;
}
.screen-1 .jumbotron-logo {
  margin-top:50px;
  margin-bottom:50px;
  min-height: 200px;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 200px 200px;
}
.screen-1 .section > div {
  max-width:600px;
  margin-left:auto;
  margin-right:auto;
  margin-top: 50px;
}
.screen-1 .section > div > p {
  color: white;
  font-size: 25px;
  font-weight: 400;
  text-align: center;
}
/*
 2nd screen
*/
.screen-2 {
  background-color: #fafafa;
  padding-top: 50px !important;
  padding-bottom: 75px !important;
}
/*
  Used to fill space in the grid.
*/
.screen-2 .vertical-spacer {
    height:50px;
}
.screen-2 .section {
  margin-left:auto;
  margin-right:auto;
  min-height:200px;
  display:table;
}
.screen-2 .section > div {
  margin-left:auto;
  margin-right:auto;
  max-width:400px;
  display:table-cell;
  vertical-align:middle;
}
.screen-2 .section h3 > i {
  margin-right:10px;
  color: #3ea5fd;
}
.screen-2 .simple-and-fast-graphic {
  background-image: url(data:image/svg+xml;base64,);
  background-position: 50% 50%;
}
.screen-2 .microprofile-support-graphic {
  background-image: url(data:image/svg+xml;base64,);
  background-position: 50% 50%;
}
.screen-2 .reactive-web-server-graphic {
  background-image: url(data:image/svg+xml;base64,);
  background-position: 50% 50%;
}
.screen-2 .observable-and-resilient-graphic {
  background-image: url(data:image/svg+xml;base64,);
  background-position: 50% 50%;
}
.screen-2 h2 {
  color: #3ea5fd;
  text-align: center;
  font-size: 40px;
  font-weight: 400;
  margin-bottom: 80px;
}
.screen-2 h3 {
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 20px;
  margin-left: 8px;
  color: #3ea5fd;
}
.screen-2 p {
  color: #3ea5fd;
  margin-left: 8px;
}
/*
 3rd screen
*/
.screen-3 {
  background-color: #41a8ff;
  padding-top: 50px !important;
  padding-bottom: 75px !important;
}
.screen-3 h2 {
  text-align: center;
  font-size: 40px;
  font-weight: 400;
  color: white;
  margin-bottom: 80px;
}
.screen-3 h4 {
  text-align: left;
  font-size: 18px;
  font-weight: 400;
  color: white;
  margin-top: 15px;
}
/*
 Hide the steps lines on small display
*/
@media screen and (min-width: 460px) {
.screen-3 .step1 {
    padding-bottom:50px;
    padding-left:65px;
    background-image: url(data:image/svg+xml;base64,);
}
.screen-3 .step2 {
    padding-bottom:50px;
    padding-left:65px;
    background-image: url(data:image/svg+xml;base64,);
}
.screen-3 .step3 {
    padding-bottom:50px;
    padding-left:65px;
    min-height:350px;
    background-image: url(data:image/svg+xml;base64,);
}
}
.screen-3 h5 {
  color: white;
  margin-top: 15px;
}
.screen-3 code {
  font-size:80%;
  font-weight: normal;
  background-color: #258bf5;
  width:100%;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  padding-left:10px;
  padding-right:10px;
  padding-top:10px;
  padding-bottom:10px;
}
/*
 Increase padding on non very small display
*/
@media screen and (min-width: 376px) {
.screen-3 code {
    font-size:100%;
    padding-left:20px;
    padding-right:20px;
}
}
.screen-3 code:after, code:before {
  content: none!important;
}
/*
 Add a padding between the code blocks on medium and more display
*/
@media screen and (min-width: 961px) {
.screen-3 .code-snippet.microframework {
    padding-right:10px;
}
.screen-3 .code-snippet.microprofile {
    padding-left:10px;
}
.screen-3 .code-snippet code {
    min-height:170px;
}
}
.screen-3 .step3 .btn {
  margin-top:15px;
  margin-left: -1.2px;
}
.screen-3 .step3 .btn__content {
  color: #3ea5fd;
}
/*
 Footer
*/
.footer {
  height:auto!important;
  padding-bottom: 30px;
  padding-top: 30px;
  padding-left: 50px;
  padding-right: 50px;
  background-color: #258bf5 !important;
}
.footer>div{
   color: white;
   width: 100%;
   text-align: center;
   padding-top: 10px;
   padding-bottom: 0px;
   padding-left: 10%;
   padding-right: 10%;
   border-top: 1px solid white;
   font-size: 0.9em;
}
