.toolbar__items .tooltip,.toolbar__items .tooltip>span{height:inherit}     
.toolbar__items .btn,.toolbar__items .menu,.toolbar__items .menu__activator{height:inherit;margin:0}     
.toolbar--card{border-radius:2px 2px 0 0;-webkit-box-shadow:0 0 0 0 rgba(0,0,0,.2),0 0 0 0 rgba(0,0,0,.14),0 0 0 0 rgba(0,0,0,.12);box-shadow:0 0 0 0 rgba(0,0,0,.2),0 0 0 0 rgba(0,0,0,.14),0 0 0 0 rgba(0,0,0,.12)}     
.toolbar--fixed{position:fixed;z-index:2}     
.toolbar--absolute,.toolbar--fixed{top:0;left:0}     
.toolbar--absolute{position:absolute;z-index:2}     
.toolbar--floating{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;margin:16px;width:auto}     
